nav,
section {
  display: block; }


h1 {
  font-size: 2em;
  margin: 0.67em 0; }



figcaption,
figure,
main {
  
  display: block; }


figure {
  margin: 1em 40px; }


hr {
  box-sizing: content-box;
  
  height: 0;
  
  overflow: visible;
   }


pre {
  font-family: monospace, monospace;
  
  font-size: 1em;
   }



a {
  background-color: transparent;
  
  -webkit-text-decoration-skip: objects;
   }


abbr[title] {
  border-bottom: none;
  
