*::after {
  box-sizing: inherit; }


blockquote,
dl,
dd,
h1,
h2,
h3,
h4,
h5,
h6,
figure,
p,
pre {
  margin: 0; }

button {
  background: transparent;
  border: 0;
  padding: 0; }


button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color; }

fieldset {
  border: 0;
  margin: 0;
  padding: 0; }

iframe {
  border: 0; }

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0; }


[tabindex="-1"]:focus {
  outline: none !important; }

*:focus {
  outline: none; }

body {
  font-family: var(--buds-font-family, "Helvetica Neue", Helvetica, Arial, "Pingfang SC", "Microsoft Yahei", Sans-serif);
  font-size: var(--buds-font-size, 14px);
  font-weight: var(--buds-font-weight, 400);
  line-height: var(--buds-line-height, 1.5);
  color: var(--buds-text-regular, #333); }

img {
  max-width: 100%; }

label {
  cursor: pointer; }

.Box {
  display: block; }

.Box--fixedHeight {
  padding-top: var(--buds-box-header-height, 3.375rem);
  padding-bottom: var(--buds-box-footer-height, 2.5rem); }
  .Box--fixedHeight,
  .Box--fixedHeight .Box-header,
  .Box--fixedHeight .Box-footer {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; }
  .Box--fixedHeight .Box-header,
  .Box--fixedHeight .Box-footer {
    overflow: auto; }
  .Box--fixedHeight .Box-body {
    height: 100%;
    overflow: auto;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; }
  .Box--fixedHeight .Box-header {
    height: var(--buds-box-header-height, 3.375rem);
    margin-top: calc(var(--buds-box-header-height, 3.375rem) * -1); }
  .Box--fixedHeight .Box-footer {
    height: var(--buds-box-footer-height, 2.5rem);
    margin-bottom: calc(var(--buds-box-footer-height, 2.5rem) * -1); }

body {
  background-color: #fefefe; }

h1 {
  margin-bottom: 15px; }

h2 {
  margin: 10px; }
  h2 + div {
    padding: 10px;
    border-top: 1px solid #a6a6a6;
    border-radius: 0 0 6px 6px;
    background-color: #fff; }

main {
  padding: 15px; }

section {
  border: 1px solid #a6a6a6;
  border-radius: 6px;
  background-color: #eee; }
  section + section {
    margin-top: 15px; }

.Box {
  background-color: antiquewhite; }
  .Box-header {
    background-color: pink; }
  .Box-body {
    background-color: yellow; }
  .Box-footer {
    background-color: aquamarine; }
